<template>
  <div class="main">
    <div class="content">
      <el-tabs type="border-card">
        <el-tab-pane>
          <template #label
            ><img src="~@/assets/img/index.svg" alt="" />
            <span> 趋势</span>
          </template>
          <div class="echarts">
            <echarts-baidu-index
              v-if="baiduIndex_flag"
              echartsBase_id="echarts_baidu_index"
              :echartsBase_width="baiduIndexWidth"
              echartsBase_height="600px"
              :echartsBase_data="baiduIndex"
              @baiduIndex_dateChange="baiduIndex_dateChange"
              @baiduIndex_cityChange="baiduIndex_cityChange"
            >
            </echarts-baidu-index>
          </div>
          <div class="echarts">
            <echarts-feed-index
              v-if="baiduFeedIndex_flag"
              echartsBase_id="echarts_baidu_feedIndex"
              :echartsBase_width="baiduIndexWidth"
              echartsBase_height="600px"
              :echartsBase_data="baidu_feedIndex"
              @feedIndex_dateChange="feedIndex_dateChange"
              @feedIndex_cityChange="feedIndex_cityChange"
            >
            </echarts-feed-index>
          </div>
        </el-tab-pane>
        <el-tab-pane label="人群">
          <template #label>
            <img src="~@/assets/img/crowd.svg" alt="" />
            <span>人群</span>
          </template>

          <div class="echarts">
            <echarts-region
              v-loading="baidu_region['loading']"
              v-if="!baidu_region['loading']"
              echartsBase_id="echarts_region"
              :echartsBase_width="crowd_width"
              echartsBase_height="400px"
              :echartsBase_data="baidu_region"
              @region_dateChange="region_dateChange"
            >
            </echarts-region>
          </div>

          <div class="echarts">
            <echarts-crowd-age
              v-if="crowdAge_flag"
              echartsBase_id="echarts_crowd_age"
              :echartsBase_width="crowd_width"
              echartsBase_height="400px"
              :echartsBase_data="crowdAge"
            ></echarts-crowd-age>
            <el-divider v-show="isIndivdual" direction="vertical"></el-divider>
            <echarts-crowd-sex
              v-if="crowdAge_flag"
              echartsBase_id="echarts_crowd_sex"
              :echartsBase_width="crowd_width"
              echartsBase_height="400px"
              :echartsBase_data="crowdSex"
            >
            </echarts-crowd-sex>
          </div>
          <div class="echarts">
            <echarts-interest
              v-if="interest_flag"
              echartsBase_id="echarts_interest"
              :echartsBase_width="baiduIndexWidth"
              echartsBase_height="600px"
              :echartsBase_data="crowd_interests"
            >
            </echarts-interest>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { GetBaiduIndex, GetCrowd, GetInterest, GetFeedIndex, GetNewIndex, GetRegion } from 'network/get_baidu_index.js'
import EchartsBaiduIndex from './EchartsBaiduIndex.vue'
import EchartsCrowdAge from './EchartsCrowdAge.vue'
import EchartsCrowdSex from './EchartsCrowdSex.vue'
import EchartsInterest from './EchartsInterest.vue'
import * as func from '@/store/mutations-type.ts'
import EchartsFeedIndex from './EchartsFeedIndex.vue'
import EchartsRegion from './EchartsRegion.vue'

export default {
  name: "Home",
  components: {
    EchartsBaiduIndex,
    EchartsCrowdAge,
    EchartsCrowdSex,
    EchartsInterest,
    EchartsFeedIndex,
    EchartsRegion
  },
  data () {
    return {
      isIndivdual: true,
      screenWidth: document.body.clientWidth - 200,
      days: 7,
      crowdAge: {
      },
      baiduIndex: {
        days: 7,
        area: '全国',
        date: [],
        userIndexes: {},
        general: {}
      },
      general: {
      },
      crowdSex: {
      },
      crowd_interests: {
      },
      baidu_feedIndex: {
        days: 7,
        area: '全国',

      },
      feedIndex_general: {
      },
      newIndex_general: {

      },
      baidu_region: {
        days: 7,
        loading: true
      },
      crowdAge_flag: false,
      baiduIndex_flag: false,
      interest_flag: false,
      baiduFeedIndex_flag: false,
      now_date: new Date()
    }
  },
  computed: {
    generals () {
      return this.general['all'] == undefined ? "" : [{
        all_avg: this.general['all'].avg,
        wise_avg: this.general['wise'].avg,
        all_yoy: this.general['all'].yoy + '%',
        all_qoq: this.general['all'].qoq + '%',
        wise_yoy: this.general['wise'].yoy + '%',
        wise_qoq: this.general['wise'].qoq + '%',
      }]
    },
    crowd_width () {
      if ((this.screenWidth - 100) / 2 < 700) {
        this.isIndivdual = false
        return Math.max(500, this.screenWidth) + 'px'
      } else {
        this.isIndivdual = true
        return ((this.screenWidth - 100) / 2) + 'px'
      }
    },
    baiduIndexWidth () {
      return Math.max(500, this.screenWidth) + 'px'
    }

  },
  methods: {
    getCrowd () {
      this.crowdAge.age = []
      this.crowdAge.rate_b = []
      this.crowdAge.rate_all = []
      let data = {}
      if (this.$store.state.token) {
        data.token = this.$store.state.token
      }
      GetCrowd(data).then(res => {
        let results = JSON.parse(res)
        for (let index in results) {
          let result = results[index]
          if (index == 0) {
            this.crowdAge['b'] = result['b']
            this.crowdAge['all'] = result['all']
            this.crowdAge['tgi'] = result['tgi']
            this.crowdAge['desc'] = result['desc']
          } else {
            this.crowdSex['b'] = result['b']
            this.crowdSex['all'] = result['all']
            this.crowdSex['tgi'] = result['tgi']
            this.crowdSex['desc'] = result['desc']
          }
        }
        this.crowdAge_flag = true
      })
    },
    getBaiduIndex () {
      let data = {
        type: this.baiduIndex['days'] == 0 ? 'live' : 'index',
        days: this.baiduIndex['days'],
        area: this.baiduIndex['area'],
      }
      if (this.$store.state.token) {
        data.token = this.$store.state.token
      }
      GetBaiduIndex(data).then(res => {
        let results = JSON.parse(res)
        let kinds = ['all', 'pc', 'wise']
        for (let result of results) {
          if (result['word'] == 'index') {
            for (let kind of kinds) {
              this.baiduIndex.userIndexes[kind] = result[kind]
            }
            this.baiduIndex.date = result['date']
          } else {
            for (let kind of kinds) {
              this.baiduIndex.general[kind] = result[kind]
              this.general[kind] = result[kind]
            }
          }
        }
        this.baiduIndex_flag = true
      })
    },
    getInterest () {
      let data = {}
      if (this.$store.state.token) {
        data.token = this.$store.state.token
      }
      GetInterest(data).then(res => {
        let results = JSON.parse(res)
        let kinds = ['all', 'b', 'desc', 'tgi']
        for (let kind of kinds) {
          this.crowd_interests[kind] = results[kind]
        }
        this.interest_flag = true
      })
    },
    getFeedIndex () {
      let data = {
        days: this.baidu_feedIndex['days'],
        area: this.baidu_feedIndex['area'],
      }
      if (this.$store.state.token) {
        data.token = this.$store.state.token
      }
      GetFeedIndex(data).then(res => {
        GetNewIndex(data).then(res => {
          let results = JSON.parse(res)
          for (let result of results) {
            if (result['word'] == 'newIndex') {
              this.baidu_feedIndex['newIndex'] = result['newIndex']
            } else {
              this.baidu_feedIndex['newIndex_general'] = result
              this.baidu_feedIndex['newIndex_general']['title'] = '新闻指数'
            }
          }
          this.baiduFeedIndex_flag = true
        })
        let results = JSON.parse(res)
        for (let result of results) {
          if (result['word'] == 'feedIndex') {
            this.baidu_feedIndex['date'] = result['date']
            this.baidu_feedIndex['feedIndex'] = result['feedIndex']
          } else {
            this.baidu_feedIndex['feedIndex_general'] = result
            this.baidu_feedIndex['feedIndex_general']['title'] = '资讯指数'
          }
        }

      })


    },
    getRegion () {
      let data = {
        days: this.baidu_region['days'],
      }
      if (this.$store.state.token) {
        data.token = this.$store.state.token
      }
      GetRegion(data).then(res => {
        let results = JSON.parse(res)
        for (let result of results) {
          console.log(result)
          this.baidu_region[result['word']] = result['data']
        }
        console.log(this.baidu_region)
        this.baidu_region['loading'] = false
      })
    },

    baiduIndex_dateChange (val) {
      this.baiduIndex['days'] = val
      this.getBaiduIndex()
    },
    baiduIndex_cityChange (val) {
      this.baiduIndex['area'] = val
      this.getBaiduIndex()
    },
    feedIndex_dateChange (val) {
      this.baidu_feedIndex['days'] = val
      this.getFeedIndex()
    },
    feedIndex_cityChange (val) {
      this.baidu_feedIndex['area'] = val
      this.getFeedIndex()
    },
    region_dateChange (val) {
      this.baidu_region['days'] = val
      this.getRegion()
    }
  },
  created () {
    this.getCrowd()
    this.getBaiduIndex()
    this.getInterest()
    this.getFeedIndex()
    this.getRegion()
  },
  mounted () {
    window.onresize = () => {
      return (() => {
        this.screenWidth = (document.body.clientWidth - 200)
      })()
    }
  }
}
</script>

<style scoped>
@import "~@/assets/css/home.css";
</style>